import axios from 'axios'
import $utils from '@/utils/index'
import root from '@/config/apiRoots'

const ENV = process.env.NODE_ENV

// axios
const service = axios.create({
  baseURL: root.baseapi, // BASE_API
  timeout: 60 * 1000 //  60s
})

// request  
service.interceptors.request.use(config => {
  // const { activity_id, uid } = $utils.getUrlParams()
  config.params = {
    ...$utils.getUrlParams(),
    ...(config.params || {})
  }
  // console.log('params', config.params, 'url', $utils.getUrlParams())
  return config
}, error => {
  Promise.reject(error)
})

// request  
service.interceptors.response.use(
  response => {
    if (response) {
      if (response.data) {
        const dmError = response.data.dm_error
        const code = response.data.code
        const msg = response.data.error_msg || response.data.msg

        if ((dmError && dmError !== 0) || (code && code !== 200)) { // status false 
          console.log(msg || '')
          return Promise.reject(response.data)
        }
      }
      return response.data
    }
  },
  error => {
    console.log(ENV === 'development' ? '' : '')
    return Promise.reject(error.response.data) // 
  })

export default service
